@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500;600&family=Raleway:wght@200;600&display=swap);
html {
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Quicksand', 'Raleway', sans-serif;
  box-sizing: border-box;
  overflow: scroll;
}

/* Estilos para motores Webkit y blink (Chrome, Safari, Opera... )*/

body::-webkit-scrollbar {
  -webkit-appearance: none;
}

body::-webkit-scrollbar:vertical {
  display: none;
}

body::-webkit-scrollbar-button:increment, body::-webkit-scrollbar-button {
  display: none;
}

body::-webkit-scrollbar:horizontal {
  display: none;
}
.father-container {
  text-align: center;
  width: 100%;
  margin: 0;
  box-sizing: border-box;
  overflow-y: hidden;
  overflow-x: hidden;
  background-color: #FBFBFB;
}

h1 .heading-display {
  font-size: 30px;
  font-family: 'Quicksand';
  color: #2D303F;
}

h1 {
  font-size: 26px;
  font-family: 'Quicksand';
  color: #2D303F;
}

h2 {
  font-size: 22px;
  color: #2D303F;
  font-family: 'Quicksand';
  margin-top: 0px;
  margin-bottom: 0px;
}

h3 {
  font-size: 18px;
  color: #2D303F;
  font-family: 'Quicksand';
}

h4 {
  font-size: 14px;
  color: #2D303F;
  margin-top: 10px;
  margin-bottom: 10px;
  font-weight: 300;
  font-family: 'Quicksand';
}

p {
  font-size: 16px;
  color: #2D303F;
  font-weight: 300;
  font-family: 'Raleway';
}

@media only screen and (min-width: 768px) {
  h1 .heading-display {
    font-size: 48px;
  }
  h1 {
    font-size: 36px;
  }
  h2 {
    font-size: 24px;
  }
  h3 {
    font-size: 20px;
  }
  h4 {
    font-size: 14px;
  }
}
.not-visible-mobile {
    display: none;
}

.box-navBar {
    display: flex;
    position: fixed;
    z-index: 100000;
    width: 100%;
    top: 0;
    right: 0;
    justify-content: flex-end;
    align-items: center;
    transition: 0.5s;
    padding-left: 10px;
    padding-right: 10px;
    background: #FF9C5B;
    border-bottom: 1px solid #C4C4C4;
    height: 80px;
}

.box-menu-desktop li {
    display: none;
}

.box-menu-desktop li a {
    text-decoration: none;
    color: #2D303F;
    font-size: 16px;
}

.box-menu-desktop li a:hover {
    color: #FF9C5B;
    ;
}

.imgLogoMenuNavegacion {
    width: 40px;
    box-sizing: border-box;
    margin: 0 auto 0px auto;
}

@media only screen and (min-width: 768px) {
    .not-visible-desktop {
        display: none;
    }
    .not-visible-mobile {
        display: block;
    }
    .box-navBar {
        display: flex;
        position: fixed;
        z-index: 100000;
        width: 100%;
        top: 0;
        right: 0;
        justify-content: flex-end;
        align-items: center;
        transition: 0.5s;
        padding-left: 80px;
        padding-right: 80px;
        background: #FFFFFF;
        border-bottom: 1px solid #C4C4C4;
        height: 80px;
        justify-content: center;
    }
    .box-menu-desktop {
        display: flex;
        justify-content: space-between;
    }
    .box-menu-desktop li {
        display: block;
        list-style: none;
        margin-left: 80px;
    }
    .box-menu-desktop li a {
        text-decoration: none;
        color: #2D303F;
        font-size: 16px;
    }
    .box-menu-desktop li a:hover {
        color: #FF9C5B;
        ;
    }
    .imgLogoMenuNavegacion {
        width: 80px;
        box-sizing: border-box;
        margin: 0 auto 0px auto;
    }
    
}
.card-filter-section {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
    box-sizing: border-box;
    margin-top: 150px;
}

.card-section {
    display: flex;
}

.card-box {
    display: flex;
    flex-flow: wrap;
}

.card {
    width: 100%;
    background-color: white;
    border-radius: 15px;
    margin: 20px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    padding: 10px;
    align-items: flex-start;
}

.img-card {
    width: 100%;
    border-radius: 15px;
    box-sizing: border-box;
    height: 150px;
    object-fit: cover;
}

.icons-card-box {
    display: flex;
    justify-content: start;
    height: 58px;
}

.icons-card {
    margin: 10px;
    display: flex;
}

.icons-card h3 {
    color: #FF9C5B;
}

.name-card-box {
    min-height: 100px;
    text-align: left;
}

.value-card-box {
    background: #FF9C5B;
    border-radius: 32px;
    height: 35px;
    min-width: 180px;
    margin-bottom: 0px;
}

.value-card-box h3 {
    color: white;
}

.text-card {
    display: flex;
    justify-content: start;
}

.text-card span {
    color: #FF9C5B;
}

@media only screen and (min-width: 768px) {
    .card-filter-section {
        padding-left: 80px;
        padding-right: 80px;
        margin-top: 150px;
    }
    .card {
        width: 200px;
        background-color: white;
        border-radius: 15px;
        margin: 20px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
}
